
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'


export default {
name: 'app',
data() {
return {
menu: [
{
name: '',
icon: 'iconfont icon-shouye',
path: 'home'
},










{
name: '',
icon: 'iconfont icon-wode',
path: 'my'
}
],
active: 0
}
},
components: {Tabbar, TabbarItem},
methods: {
onChange(index) {
this.$router.push({name: this.menu[index].path})
}
},
created() {
this.active = this.menu.findIndex(m => m.path == this.$route.name)
}
}



