
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

import { mapState } from 'vuex'
import Service from '@/services'

const model = () => {
return {
hk_type: null,
hk_address: '',
jt_address: '',
callname: '',
danwei: '',
name: '',
phonenum: '',
user_id: ''
}
}

export default {
name: "StepOne",
computed: {
...mapState(['token'])
},
components: {
XButton,
Group,
XInput,
Selector,
Datetime,
Divider
},
data() {
return {
form: model(),
hk: [
{key:0, value:''},
{key:1, value:''}
]
}
},
methods: {
isValid() {
let valid = true
const fields = model()
Object.keys(fields).forEach(field => {
if(this.$refs[field]) {
console.log(this.$refs[field])
if(this.$refs[field].valid == false) {
valid = false
}
}
})
return valid
},

onSubmit() {
if(this.isValid()) {
this.$vux.loading.show()
this.form.user_id = this.token
Service.setuserfamilyinfo(this.form).then(rep => {
this.$vux.loading.hide()
this.$emit('submit')
}).catch(()=> {
this.$vux.loading.hide()
})
}else {
this.$vux.toast.show({
text: '',
width: '5rem',
type: 'warn'
})
}
}
},
mounted() {
}
}



