import { mapMutations } from 'vuex'
export default {
  name: "Main",
  methods: {
    ...mapMutations(['setToken'])
  },
  created() {
    const token = this.$ls.get('token', null)
    this.setToken(token)
    if (token) {
      this.$router.push({name: 'home'})
    }else {
      this.$router.push({name: 'login'})
    }
  }
}



