import Service from '@/services'

export default {
  data() {
    return {
      form: {
        username: '',
        password: '',
        authcode: ''
      },
      password2: '',
      time: 0
    }
  },
  computed: {
    btnName() {
      if(this.time > 0) {
        return this.time + ''
      }else {
        return ''
      }
    }
  },
  methods: {
    onBack() {
      this.$router.push({ name: "login" });
    },
    getAuthCode() {
      if(!(/^1[3|4|5|8][0-9]\d{4,8}$/.test(this.form.username))){
        return this.$vux.toast.show({
          text: '',
          width: '10em',
          type: 'text',
          position: 'top'
        })
      }
      this.time = 60

      this.timer = setInterval(() => {
        this.time--
        if(this.time == 0) {
          clearInterval(this.timer)
        }
      }, 1000)

      Service.getAuthCode({
        phonenum: this.form.username,
        method: 'register'
      }).then(rep => {
        this.form.authcode = rep
      })
    },
    onSubmit() {
      this.$validator.validate().then(valid => {
        if(valid) {

          if(this.form.password != this.password2) {
            return this.$vux.toast.show({
              text: '',
              width: '10em',
              type: 'text',
              position: 'top'
            })
          }

          this.$vux.loading.show({text: ''})
          Service.resetpsd(this.form).then(rep => {
            this.$vux.loading.hide()
            this.$vux.alert.show({
              title: '',
              content: rep,
              onHide: () => {
                this.onBack()
              }
            })
          }).catch(()=> {
            this.$vux.loading.hide()
          })
        }

      })
    }
  }
};



