
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'

import { mapState } from 'vuex'
import Service from '@/services'

const model = () => {
return {
areaid: null,
idcard: '',
realname: '',
sexid: null,
mz_id: null,
political_id: null,
shenti: '',
wenhua_id: null,
address: '',
phonenum: '',
user_id: ''
}
}

export default {
name: "StepZero",
computed: {
...mapState(['token'])
},
components: {
XButton,
Group,
XInput,
Selector,
Datetime
},
data() {
return {
form: model(),
sex: [
{key:1, value:''},
{key:0, value:''}
],
political: [
{ key: 0, value: '' },
{ key: 1, value: '' },
{ key: 2, value: '' },
],
wenhua: [
{ key: 0, value: '' },
{ key: 1, value: '' },
{ key: 2, value: '' },
{ key: 3, value: '' },
{ key: 4, value: '' },
{ key: 5, value: '' }
],
mz: [
{ key: 0, value: ''}
],
shenti: [
{ key: 0, value: ''}
]
}
},
methods: {
setArea(area) {
this.form.areaid = area
},
isValid() {
let valid = true
const fields = model()
Object.keys(fields).forEach(field => {
if(this.$refs[field]) {
console.log(this.$refs[field])
if(this.$refs[field].valid == false) {
valid = false
}
}
})
return valid
},

onSubmit() {
if(this.isValid()) {
this.$vux.loading.show()
this.form.user_id = this.token
Service.setuserbaseinfo(this.form).then(rep => {
this.$vux.loading.hide()
this.$emit('submit')
}).catch(()=> {
this.$vux.loading.hide()
})
}else {
this.$vux.toast.show({
text: '',
width: '5rem',
type: 'warn'
})
}
}
},
mounted() {
}
}



