
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import { mapState } from 'vuex'
import Service from '@/services'

const model = () => {
return {
job_old: '',
out_address: '',
finishdate: '',
job_good: '',
ydwd: null,
finishtype: null,
peixuntype: null,
allow: null
}
}

export default {
name: "StepTwo",
computed: {
...mapState(['token'])
},
components: {
XButton,
Group,
XInput,
Selector,
Datetime,
Divider,
XTextarea
},
data() {
return {
form: model(),
ydwd: [
{key:1, value:''},
{key:2, value:''},
{key:0, value:''},
],
allow: [
{key:0, value:''},
{key:1, value:''},
],
peixuntype: [
{key:1, value:''},
{key:2, value:''},
{key:0, value:''},
],
finishtype: [
{key:1, value:''},
{key:2, value:''},
{key:3, value:''},
{key:0, value:''}
]
}
},
methods: {
isValid() {
let valid = true
const fields = model()
Object.keys(fields).forEach(field => {
if(this.$refs[field]) {
console.log(this.$refs[field])
if(this.$refs[field].valid == false) {
valid = false
}
}
})
return valid
},

onSubmit() {
if(this.isValid()) {
this.$vux.loading.show()
this.form.user_id = this.token
Service.setuserenterinfo(this.form).then(rep => {
this.$vux.loading.hide()
this.$emit('submit')
}).catch(()=> {
this.$vux.loading.hide()
})
}else {
this.$vux.toast.show({
text: '',
width: '5rem',
type: 'warn'
})
}
}
},
mounted() {
}
}



